(function () {
  'use strict';

  angular
    .module('app', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngRoute', 'ui.bootstrap', 'app.core']);

})();

(function () {
  'use strict';

  angular
    .module('app')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(geoLocationService, weatherService) {
    var vm = this;
    vm.location = geoLocationService.location;
    activate();

    function activate() {
      getWeatherByLocation();
    }

    function getWeatherByLocation() {
      geoLocationService.getLocation(vm.location).then(weatherService.getWeather(vm.location));
    }

  }
})();

(function () {
  'use strict';

  angular
    .module('app.core', []);
})();

(function () {
  'use strict';
  angular
    .module('app.core')
    .factory('weatherService', weatherService);

  function weatherService($http, WEATHER_KEY) {
    var service = {
      getWeather: getWeather
    };

    return service;

    ////////////////////

    function getWeather(location) {
      return $http.get('http://api.openweathermap.org/data/2.5/weather?&units=metric&q=' + location.city + ',' + location.countryCode + '&APPID=' + WEATHER_KEY).success(function (weatherData) {
        location.weatherData = weatherData;
        location.weatherIcon = 'http://openweathermap.org/img/w/' + weatherData['weather'][0].icon + '.png';
        location.weather = weatherData['weather'][0].main;
        location.tempC = weatherData['main'].temp;
        location.tempF = (location.tempC * (9 / 5)) + 32;
      });
    }
  }
})();

(function () {
  'use strict';
  angular
    .module('app.core')
    .factory('geoLocationService', geoLocationService);

  function geoLocationService($http) {

    var location = {
      tempF: '90',
      tempC: '90',
      celsius: false,
      city: '',
      country: '',
      lat: '',
      long: '',
      weather: '',
      weatherData: '',
      weatherIcon: '',
      region: ''
    };

    var service = {
      location: location,
      getLocation: getLocation
    };

    return service;

    ////////////////////

    function getLocation(location) {
      return $http.get('http://ip-api.com/json')
        .success(function (coordinates) {
          location.city = coordinates.city;
          location.country = coordinates.country;
          location.countryCode = coordinates.countryCode;
          location.region = coordinates.region;
        });
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('app')
    .run(runBlock);

  /** @ngInject */
  function runBlock() {

  }

})();

(function () {
  'use strict';

  angular
    .module('app')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

(function () {
  'use strict';

  angular
    .module('app')
    .constant('WEATHER_KEY', 'ced22edda5ab59a89469af8a36ce60af');
})();

(function () {
  'use strict';

  angular
    .module('app')
    .config(config);

  /** @ngInject */
  function config($logProvider, $httpProvider) {
    // Enable log
    $logProvider.debugEnabled(true);
    $httpProvider.defaults.useXDomain = true;
    delete $httpProvider.defaults.headers.common['X-Requested-With'];
    // Set options third-party lib

  }

})();

angular.module("fccLocalWeather").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=container><div class=\"jumbotron text-center\"><h1>Hello fair citizen of {{vm.location.city}}, {{vm.location.region}}</h1><p>Your current temperature is {{vm.location.celsius ? vm.location.tempC : vm.location.tempF}} <i ng-click=\"vm.location.celsius = !vm.location.celsius\"><span ng-show=vm.location.celsius>C</span> <span ng-hide=vm.location.celsius>F</span> </i></p><p>and it is <img src={{vm.location.weatherIcon}} alt=\"\"> ({{vm.location.weather}}) outside!</p></div><div class=\"box on\" ng-show=toggle ng-animate=\"\'box\'\">On</div><p id=footer>Made with <span class=stylish>style</span> by <a href=http://ric.mclaughlin.today>Ric McLaughlin</a></p></div>");}]);